/* ======= popup start ======= */
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 999;
    background-color: var(--color-violet);
    display: none;
    justify-content: center;
    align-items: center;

    &__title {
        font-size: 35px;
    }

    &__form {
        width: 685px;
        padding: 25px 15px;
        box-shadow: 0px 0px 85px 11px var(--color-hover-button);
        position: relative;
    }

    &__close {
        width: 40px;
        height: 30px;
        top: 20px;
        right: 40px;
        cursor: pointer;
        position: absolute;

        span {
            display: inline-block;
            width: 100%;
            height: 3px;
            background-color: var(--color-white);

            &:first-child {
                transform: rotate(45deg) translateY(13px);

            }

            &:last-child {
                transform: rotate(-45deg) translateY(-13px);
            }
        }
    }

    &__form-form {
        margin-top: 47px;
        display: flex;
        flex-direction: column;
    }

    &__form-header {
        display: flex;
        justify-content: space-between;
    }

    &__form-body {
        margin-top: 19px;
        display: flex;
        flex-direction: column;

        textarea {
            margin-top: 8px;
            padding-top: 32px;
            padding-left: 13px;
            max-width: 670px;
            height: 125px;
            border: 1px solid #ccc;

            &:focus {
                outline-color: var(--color-gold);
            }
        }
    }

    &__form-button {
        margin-top: 20px;
    }

    &__form-input {
        margin-top: 10px;
        width: 320px;
        height: 80px;
        padding-left: 16px;
        border: 1px solid #ccc;

        &:focus {
            outline-color: var(--color-gold);
        }
    }

    &__form-label,
    &__form-body {
        color: var(--color-black)''
    }

    &__form-button {
        width: 270px;
        height: 100px;
        font-size: 24px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        color: var(--color-white);
        background-color: var(--color-hover-button);
        transition: .5s;
        border: none;

        &:hover {
            transition: .5s;
            background-color: var(--color-hover-button);
        }
    }
}

/* ======= popup media start ======= */

@media screen and (max-width: 686px) {
    .popup {
        &__form-header {
            flex-direction: column;
            gap: 20px;
        }

        &__form-label {
            display: flex;
            flex-direction: column;
            width: fit-content;
            width: 100%;
        }

        &__form-input {
            height: 60px;
            width: 100%;
        }

        &__form-button {
            height: 70px;
            align-self: center;
        }

        &__form-body {
            textarea {
                max-width: 100%;
            }
        }
    }
}