/* ======= atmosphere start ======= */
.atmosphere {
    padding-top: 82px;
    background: linear-gradient(180deg, var(--color-violet) 60%, var(--color-white) 40%);
    color: var(--color-white);

    &__container {
        position: relative;
    }

    &__title {
        font-size: 48px;
        font-style: normal;
        font-weight: 700;
        // line-height: 88px;
    }

    &__text {
        width: 569px;
        margin-top: 8px;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px;
    }

    &__swiper {
        max-width: 100%;
        margin-top: 50px;
        height: 646px;
    }

    &__pic {
        height: 100%;
    }

    &__navigation {
        position: absolute;
        right: 30px;
        top: 57px;
        width: 132px;
        display: flex;
        justify-content: space-between;
    }

    &__button {
        position: static;
        color: var(--color-gold);
    }
}

.swiper-slide {
    width: 60%;
}

.swiper-slide:nth-child(1n) {
    width: 40%;
}

.swiper-slide:nth-child(2n) {
    width: 60%;
}

.swiper-slide:nth-child(3n) {
    width: 40%;
}

/* ======= atmosphere media start ======= */
@media screen and (max-width: 675px) {
    .atmosphere {
        &__navigation {
            top: -30px;
            right: 10px;
        }
    }
}

@media screen and (max-width: 570px) {
    .atmosphere {
        &__text {
            width: 100%;
        }
    }
}