:root {
    // base
    --font: "Poppins", sans-serif;
    --content-width: 1170px;
    --container-offset: 10px;
    --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

    // colors
    --color-white: #fff;
    --color-black: #000;
    --color-gold: #d3b47f;
    --color-hover: #8f8f8f;
    --color-grey1: #2e2e2e;
    --color-grey: #808080;
    --color-violet: #3A4574;
    --color-hover-button: #182040;
}