/* ======= send-popup start ======= */
.send-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 999;
    background-color: var(--color-violet);
    display: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 15px;
    padding-right: 15px;

    &__text {
        width: 685px;
        padding: 25px 15px;
        border-radius: 10px;
        background-color: var(--color-white);
        box-shadow: 0px 0px 85px 11px var(--color-hover-button);
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__title {
        font-size: 35px;
    }

    &__close {
        width: 40px;
        height: 30px;
        cursor: pointer;

        span {
            display: inline-block;
            width: 100%;
            height: 3px;
            background-color: var(--color-violet);

            &:first-child {
                transform: rotate(45deg) translateY(13px);

            }

            &:last-child {
                transform: rotate(-45deg) translateY(-13px);
            }
        }
    }
}

/* ======= send-popup start ======= */

@media screen and (max-width: 465px) {
    .send-popup {
        &__text {
            flex-direction: column-reverse;
            gap: 30px;
        }

        &__title {
            font-size: 22px;
        }
    }
}