/* ======= mission start ======= */
.mission {
    padding-top: clamp(4.375rem, 3.887rem + 2.44vw, 6.813rem);
    padding-bottom: clamp(3.125rem, 2.787rem + 1.69vw, 4.813rem);

    &__block {
        display: flex;
        justify-content: space-between;
        gap: 30px;

        &:not(:first-child) {
            margin-top: 25px;
        }
    }

    &__block-left {
        width: 40%;
        min-hedth: 100%;
        padding-top: clamp(3.125rem, -4.882rem + 14.64vw, 12.688rem);
    }

    &__title {
        font-size: clamp(1.375rem, 1.212rem + 0.81vw, 2.188rem);
        font-style: normal;
        font-weight: 700;
        color: var(--color-black);
        position: relative;

        &:before {
            content: '';
            position: absolute;
            width: 108px;
            height: 1px;
            background-color: var(--color-gold);
            bottom: -28px;
            left: -52px;
        }
    }

    &__text {
        margin-top: 45px;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px;
        color: var(--color-grey);
    }

    &__picture {
        width: clamp(29.375rem, 26.875rem + 12.5vw, 41.875rem);
        height: clamp(29.375rem, 28.25rem + 5.63vw, 35rem);
    }
}

/* ======= mission media start ======= */
@media screen and (max-width: 875px) {
    .mission {
        &__title {
            &:before {
                bottom: -8px;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }
}

@media screen and (max-width: 675px) {
    .mission {
        &__block {
            flex-direction: column;

            &-body {
                flex-direction: column-reverse;
            }
        }

        &__block-left {
            width: 100%;
            padding-top: 0;
        }

        &__title {
            text-align: center;
        }

        &__text {
            margin-top: clamp(1.25rem, -0.158rem + 7.04vw, 2.813rem);
        }
    }
}