/* ======= footer start ======= */
.footer {
    padding-top: 40px;
    padding-bottom: 40px;

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__social-list {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        gap: 25px;

        li {
            width: 48px;
            height: 48px;
            border-radius: 100%;
            border: 1px solid var(--color-black);
            transition: .5s;

            &:hover {
                transition: .5s;
                border-color: var(--color-gold);
            }
        }
    }

    &__social-link {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: var(--font);
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: 24px;
        color: var(--color-black);
        transition: .5s;

        &:hover {
            transition: .5s;
            color: var(--color-gold);
        }
    }
}

/* ======= footer media start ======= */
@media screen and (max-width: 605px) {
    .footer {
        &__container {
            flex-direction: column;
            gap: 30px;
        }
    }
}