/* ======= contacts start ======= */
.contacts {
    padding-top: clamp(3.125rem, 2.25rem + 4.38vw, 7.5rem);
    padding-bottom: 50px;

    &__container {
        display: flex;
        justify-content: space-between;
    }

    &__contacts {
        width: 40%;
    }

    &__title {
        font-size: 48px;
        font-style: normal;
        font-weight: 700;
        color: var(--color-black);
    }

    &__list {
        color: var(--color-grey1);
        list-style: none;
        margin-top: 50px;
    }

    &__list-bottom {
        margin-top: 40px;
        display: flex;
        flex-direction: column;
        gap: 16px;
    }

    &__item {
        display: flex;
        align-items: center;
        gap: 16px;

        &:not(:first-child) {
            margin-top: 40px;
        }
    }

    &__wrap-icon {
        width: 64px;
        height: 64px;
        border-radius: 100%;
        background-color: var(--color-gold);
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__icon {
        width: 24px;
        height: 24px;
        background-color: var(--color-gold);

        use {
            fill: transparent;
            stroke: var(--color-white);
        }
    }

    &__wrapper-text {
        display: flex;
        flex-direction: column;
    }

    &__label {
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px
    }

    &__name {
        font-size: 24px;
        font-style: normal;
        font-weight: 500;
        line-height: 32px;
    }

    &__form {
        width: 57%;
    }

    &__form-form {
        margin-top: 47px;
        display: flex;
        flex-direction: column;
    }

    &__form-header {
        display: flex;
        justify-content: space-between;
    }

    &__form-body {
        margin-top: 19px;
        display: flex;
        flex-direction: column;

        textarea {
            margin-top: 8px;
            padding-top: 32px;
            padding-left: 13px;
            max-width: 670px;
            height: 125px;
            border: 1px solid #ccc;

            &:focus {
                outline-color: var(--color-gold);
            }
        }
    }

    &__form-button {
        margin-top: 20px;
    }

    &__form-input {
        margin-top: 10px;
        width: 320px;
        height: 80px;
        padding-left: 16px;
        border: 1px solid #ccc;

        &:focus {
            outline-color: var(--color-gold);
        }
    }

    &__form-label,
    &__form-body {
        color: var(--color-black)''
    }

    &__form-button {
        width: 270px;
        height: 100px;
        font-size: 24px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        color: var(--color-white);
        background-color: var(--color-violet);
        transition: .5s;
        border: none;

        &:hover {
            transition: .5s;
            background-color: var(--color-hover-button);
        }
    }
}

/* ======= contacts media start ======= */
@media screen and (max-width: 1175px) {
    .contacts {
        &__container {
            flex-direction: column;
            align-items: center;
            gap: 80px;
        }

        &__contacts {
            width: 70%;
        }

        &__wrapper-list {
            display: flex;
            justify-content: space-between;
        }

        &__list {
            margin-top: 40px;
            display: flex;
            flex-direction: column;
            gap: 16px;
        }

        &__item:not(:first-child) {
            margin-top: 0;
        }

        &__item {
            margin-bottom: 20px;
        }

        &__form {
            width: 70%;
        }

        &__form-label {
            display: flex;
            flex-direction: column;
            width: 48%;
        }

        &__form-input {
            width: 100%;
        }

        &__form-body {
            textarea {
                max-width: 100%;
            }
        }
    }
}

@media screen and (max-width: 980px) {
    .contacts {
        &__contacts {
            width: 90%;
        }

        &__form {
            width: 90%;
        }
    }
}

@media screen and (max-width: 760px) {
    .contacts {
        &__contacts {
            width: 100%;
        }

        &__container {
            gap: 60px;
        }

        &__title {
            font-size: 36px;
        }

        &__wrapper-list {
            flex-direction: column;
        }

        &__list {
            gap: 0;
        }

        &__list-bottom {
            margin-top: 0;
            gap: 0;
        }

        &__wrap-icon {
            width: 50px;
            height: 50px;
        }

        &__label {
            font-size: 16px;
            line-height: 20px;
        }

        &__name {
            font-size: 18px;
            line-height: 22px;
        }

        &__form {
            width: 100%;
        }
    }
}

@media screen and (max-width: 530px) {
    .contacts {
        &__form-header {
            flex-direction: column;
            gap: 20px;
        }

        &__form-label {
            width: 100%;
        }

        &__form-input {
            height: 60px;
        }

        &__form-button {
            height: 70px;
            align-self: center;
        }
    }
}