/* ======= advantages start ======= */
.advantages {
    width: 100%;
    background-color: var(--color-violet);
    padding-top: 219px;
    padding-bottom: 171px;

    &__swiper {
        width: 80%;
    }

    &__slide {
        width: 790px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        color: var(--color-gold);
    }

    &__svg {
        width: 83px;
        height: 58px;
    }

    &__svg-icon {
        width: 100%;
        height: 100%;

        use {
            fill: var(--color-gold);
        }
    }

    &__title {
        margin: 0px;
        margin-top: 37px;
        font-size: 36px;
        font-style: normal;
        font-weight: 600;
        line-height: 40px;
        letter-spacing: 1px;
    }

    &__text {
        margin: 0px;
        margin-top: 20px;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 40px;
        color: var(--color-white);
    }

    &__author {
        margin: 0px;
        margin-top: 29px;
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 40px;
        letter-spacing: 5px;
    }

    &__swiper-button::after {
        color: var(--color-gold);
    }

    &__navigation {
        position: relative;
        width: 99%;
        height: 50px;
        top: -185px;
        margin-left: 5px;
        z-index: 6;

        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__swiper-button {
        position: static;
        margin-top: 0;
    }
}

.swiper-slide {
    display: flex;
    justify-content: center;
}

/* ======= advantages start media ======= */
@media screen and (max-width: 940px) {
    .advantages {
        padding-top: clamp(3.125rem, -2.327rem + 27.26vw, 13.688rem);
        padding-bottom: clamp(6.25rem, 2.411rem + 19.19vw, 13.688rem);

        &__navigation {
            gap: 40px;
            width: 300px;
            top: 50px;
            margin-left: auto;
            margin-right: auto;
        }

        &__title {
            font-size: clamp(1.563rem, 1.175rem + 1.94vw, 2.313rem);
        }

        &__author {
            font-size: clamp(1.125rem, 0.931rem + 0.97vw, 1.5rem);
        }
    }
}

@media screen and (max-width: 450px) {
    .advantages {
        &__svg {
            display: none;
        }

        &__title {
            margin-top: 0;
        }

        &__swiper {
            width: 100%;
        }
    }
}