/* ======= hero start ======= */
.hero {
    width: 100%;
    background-color: var(--color-black);
    color: var(--color-white);
    padding-bottom: clamp(6.25rem, 4.188rem + 10.31vw, 16.563rem);
    position: relative;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.59) 0%, rgba(0, 0, 0, 0.59) 100%), url(../img/hero-bg.png), lightgray 50% / cover no-repeat;

    &__subtitle {
        display: inline-block;
        margin-top: clamp(7.813rem, 6.075rem + 8.69vw, 16.5rem);
        font-size: clamp(1.125rem, 1.05rem + 0.38vw, 1.5rem);
        font-style: normal;
        font-weight: 500;
        line-height: 32px;
        letter-spacing: 1.44px;
    }

    &__title {
        margin: 24px 0 40px 0;
        font-size: clamp(1.875rem, 1.033rem + 2.47vw, 4rem);
        font-style: normal;
        font-weight: 700;
        line-height: clamp(2.5rem, 1.9rem + 3vw, 5.5rem);

    }

    &__navigation {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__button {
        width: 270px;
        height: clamp(4.375rem, 4rem + 1.88vw, 6.25rem);
        background-color: var(--color-violet);
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: 24px;
        color: var(--color-hover);
        transition: .5s;

        &:hover {
            transition: .5s;
            color: var(--color-white);
            background-color: var(--color-hover-button);
        }
    }

    &__social-list {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        gap: 25px;

        li {
            width: 48px;
            height: 48px;
            border-radius: 100%;
            border: 1px solid var(--color-white);
            transition: .5s;

            &:hover {
                transition: .5s;
                border-color: var(--color-gold);
            }
        }
    }

    &__social-link {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: var(--font);
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: 24px;
        color: var(--color-white);
        transition: .5s;

        &:hover {
            transition: .5s;
            color: var(--color-gold);
        }
    }
}


/* ======= hero start media ======= */

@media screen and (max-width: 600px) {
    .hero {
        // padding-bottom: 80px;

        &__navigation {
            flex-direction: column;
            gap: 60px;
        }
    }
}