/* ======= food start ======= */
.food {
    width: 100%;
    background-color: var(--color-white);
    padding-top: 80px;
    padding-bottom: 90px;

    &__container {
        display: flex;
        justify-content: space-between;
    }

    &__text {
        width: 43%;
    }

    &__text-list {
        list-style: none;
        color: var(--color-black);
        margin: 0;
        padding: 0;
    }

    &__text-item {
        border-bottom: 1px solid #e7e7e7;

        &:not(:first-child) {
            margin-top: 19px;
        }
    }

    &__item-title {
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 32px;
        letter-spacing: 1px;
    }

    &__item-menu {
        margin-top: 10px;
        margin-bottom: 20px;
    }

    &__item-dish {
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px;
    }

    &__item-hyphen {
        display: inline-flex;
        justify-content: flex-start;
        align-items: center;
        gap: 12px;
        margin-left: 6px;
        margin-right: 3px;
    }

    &__item-icon {
        width: 8px;
        height: 8px;

        use {
            fill: var(--color-gold);
        }
    }

    &__image {
        width: 57%;
        height: 636px;
        position: relative;
    }

    &__picture {
        width: 323px;
        height: 486px;
    }

    &__picture-left {
        position: absolute;
        bottom: 0;
        left: 0px;
        z-index: 2;
    }

    &__picture-right {
        position: absolute;
        top: 0;
        right: 0;
    }

    &__border {
        width: 476px;
        height: 502px;
        border: 2px solid var(--color-gold);
        position: absolute;
        top: 48%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

/* ======= food start media ======= */

@media screen and (max-width: 1130px) {
    .food {
        &__container {
            gap: 10px;
        }
    }
}

@media screen and (max-width: 870px) {
    .food {
        padding-top: clamp(2.5rem, 1.045rem + 7.27vw, 5rem);
        padding-bottom: clamp(2.813rem, 1.176rem + 8.18vw, 5.625rem);

        &__container {
            justify-content: center;
        }

        &__text {
            width: 500px;
        }

        &__image {
            display: none;
        }
    }
}

@media screen and (max-width: 500px) {
    .food {
        &__item-menu {
            display: flex;
            flex-direction: column;
        }

        &__item-title {
            font-size: clamp(1.25rem, 0.806rem + 2.22vw, 1.5rem);
        }

        &__item-hyphen {
            justify-content: center;
        }

        &__text {
            width: 100%;
            text-align: center;
        }

        &__item-dish {
            font-size: clamp(1rem, 0.778rem + 1.11vw, 1.125rem);
        }

    }
}