/* ======= header start ======= */
.header {
    position: absolute;
    z-index: 2;
    width: 100%;
    top: 55px;

    &__logo {
        width: 269px;
        height: 55px;
    }

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__menu {
        margin: 0;
        padding: 0;
        margin-left: -67px;
        list-style: none;
        display: flex;
        gap: 45px;
    }

    &__link {
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        color: var(--color-white);
        transition-duration: .5s;

        &:hover {
            transition-duration: .5s;
            color: var(--color-hover);
        }
    }

    &__burger {
        width: 40px;
        height: 30px;
        display: none;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        span {
            width: 100%;
            height: 2px;
            background-color: var(--color-white);
        }

    }



    &__phone {
        display: flex;
        align-items: center;
    }

    &__phone-svg {
        width: 24px;
        height: 24px;
        margin-right: 17px;

        use {
            fill: var(--color-gold);
            stroke: var(--color-gold);
        }
    }

    &__phone-mobile {
        display: none;
    }
}

/* ======= header start media======= */
@media screen and (max-width: 941px) {
    .header {
        &__menu {
            width: 300px;
            height: fit-content;
            background-color: var(--color-hover);
            position: absolute;
            top: -57px;
            left: -350px;
            padding-left: 40px;
            padding-top: 80px;
            padding-bottom: 40px;
            flex-direction: column;

        }

        &__link {
            &:hover {
                color: var(--color-gold);
            }
        }

        &__burger-close {
            width: 40px;
            height: 30px;
            position: absolute;
            top: 20px;
            left: 40px;

            span {
                display: inline-block;
                width: 100%;
                height: 3px;
                background-color: var(--color-white);

                &:first-child {
                    transform: rotate(45deg) translateY(13px);

                }

                &:last-child {
                    transform: rotate(-45deg) translateY(-13px);
                }
            }
        }

        &__phone-svg-mobile {
            width: 7px;
            height: 7px;
            margin-right: 10px;

            use {
                fill: var(--color-gold);
            }
        }

        &__burger {
            display: flex;
        }
    }
}

@media screen and (max-width: 543px) {
    .header {

        &__logo {
            display: none;
        }
    }
}